.image {
  width: 25vw;
  border-radius: 30%;
  border-style: solid;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.5);
}


@media (max-width: 768px) {
  .centerImage {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 60vw;
  }
}

.full-width-hr {
  width: 100%;
}